body,div,ul,li,a,p,h2,span {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 0;
}
ul {
    list-style: none;
}
a {
    text-decoration: none;
}
.wrapper {
    position: relative;
    min-width: 360px;
}
.logo {
    max-width: 960px;
    margin: 0 auto;
    position: relative;
    width: 100%;
    background-color: royalblue;
    text-align: center;
    z-index: 10;
}
.homeLink {
    display: inline-block;
    width: 100%;
    height: 48px;
    font-size: 26px;
    font-weight: bold;
    line-height: 48px;
    color: white;
    text-decoration: none;
}
.mainMenu {
    margin: 0 auto;
    max-width: 960px;
    position: relative;
    height: 48px;
    z-index: 10;
}
.mainMenu > ul {
    position: relative;
}
.mainMenu > ul > li {
    position: relative;
    width: 25%;
    float: left;
    background-color: #ccc;
    text-align: center;
}
.mainMenu a {
    position: relative;
    display: inline-block;
    font-size: 18px;
    font-weight: bold;
    color: navy;
    width: 100%;
    height: 48px;
    line-height: 48px;
}
.mainMenu > ul > li .active {
    background-color: white;
}
.background {
    position: relative;
    min-height: 100vh;
    background: url("/src/image/homeback.png");
}
.content {
    position: relative;
    max-width: 960px;
    margin: 0 auto;
}
.home {
    position: relative;
    width: 100%;
    height: auto;
}
.home p {
    text-align: center;
    padding: 48px 12px 0;
    font-size: 24px;
    font-weight: bold;
    word-break: keep-all;
}
.sub {
    background-color: #fff;
}
.sub > ul > li {
    border-bottom: 1px solid #666;
}
.sub h2 {
    position: relative;
    font-size: 24px;
    padding: 9px 12px;
    color: green;
}
.sub > ul > li > h2 > span {
    font-size: 20px;
}
.sub > ul > li > h2 > p {
    display: inline-block;
    position: relative;
    font-size: 12px;
    color: red;
}
.sub > ul > li > ul > li {
    border-top: 1px solid #666;
}
.sub > ul > li > ul > li > p {
    font-size: 16px;
    font-weight: bold;
    color: mediumblue;
    padding: 9px 16px;
}
.sub > ul > li > ul > li > p:hover {
    cursor: pointer;
}
.sub > ul > li > ul > li:active {
    background-color: azure;
}
.footer {
    background-color: #ccc;
    padding: 16px 0;
}
.footer p {
    text-align: center;
    font-size: 14px;
    font-weight: bold;
}